/* debut de nos avis */
.avis {

    &__title {
        @include titre()
    }

    padding: {
        left: 4vw;
        right: 4vw;
    }

    .card {
        // margin: 0 auto;
        display: flex;
        flex-direction: column;
        // align-items: center;
        opacity: 1;
        // background-color: #fafaff;
        transition: opacity 0.3s ease-in-out;
        color: $bleu;
        box-shadow: rgba(17, 17, 26, 0.05) 0px 4px 16px,
            rgba(17, 17, 26, 0.05) 0px 8px 32px;
        border: 1px solid $bleu;
        padding: 4vh 2vw;
        gap: 5vh;
        border-radius: 10px;
        margin-bottom: 5vh;

        padding-left: 5vw;
        padding-right: 5vw;

        &__image {
            border-radius: 70%;
        }

        &__nom {
            font-family: 'Poppins';
            font-size: clamp(20px, 1.4rem, 40px);
            font-weight: 800;
            margin-bottom: 2vh;
        }

        &__job {
            font-family: 'Poppins', sans-serif;
            // text-align: center;
            font-size: clamp(17px, 1.4rem, 40px);

        }

        &__avis {
            // text-align: justify;
            line-height: 1.2;
            font-size: clamp(17px, 1.5rem, 40px);
            height: 10vh;
        }

        &__description {
            display: flex;
            align-items: center;
            // gap: 10vw;

        }

        &__notes {
            // margin-top: 3vh;
            margin-bottom: 2vh;
            padding-bottom: 5vh;
            border-bottom: 0.5px $bleu solid;
        }


    }

}

.leSvg {
    display: flex;
    position: relative;
    top: -20vh;
    width: 100vw;
    height: 20vh;
}


.leSvg__haut {
    // @extend .leSvg;
    display: flex;
    position: relative;
    top: -50vh;
    width: 100vw;
    height: 0;
}

// fin avisss