

.projet1 {
    display: flex;
    flex-direction: column;

    &__logo {
        width: 10%;
        margin-bottom: -8rem;
    }


    .rectangle {
        margin-top: 15rem;
        width: 100%;
        height: 150px;
        background: #01254C;
        z-index: -1;

    }

    .figure {
        display: flex;

        &__img {
            width: 30%;
            position: absolute;
            margin-top: -23rem;
            margin-left: 5rem;
        }

        &__figcaption {
            display: flex;
            flex-direction: row;



            &__content {
                position: absolute;
                color: #EFEBEB;
                font-size: large;
                padding-top: 20%;
                margin-top: -40rem;
                margin-left: 35em;



            }
        }
    }
}

.projet2 {
    display: flex;
    flex-direction: column;
    margin-top: 20rem;


    &__logo {
        margin-bottom: -15rem;
        width: 10%;
        margin-left: 67%;
    }




    .rectangle {
        margin-top: 25rem;
        width: 100%;
        height: 150px;
        background: #01254C;
        z-index: -1;

    }


    .figure {
        margin-bottom: 20rem;
        display: flex;
        flex-direction: row-reverse;

        &__img {
            margin-top: -25rem;
            width: 30%;
            position: absolute;
        }

        &__figcaption {
            display: flex;
            flex-direction: row;



            &__content {
                margin-left: -68em;
                margin-top: -40rem;
                position: absolute;
                color: #EFEBEB;
                font-size: large;
                padding-top: 20%;

            }
        }
    }

}